<template>
  <div class="pomodoro-container">
    <div class="pomodoro-buttons">

      <button v-if="buttonOnStart" class="pomodoro-work" @click="timerMode('work')" :disabled="buttonBefore">Работа</button>
      <button v-else class="pomodoro-work-active">Работа</button>

      <button v-if="buttonOnPause" class="pomodoro-break" @click="timerMode('break')" :disabled="buttonBefore">Отдых</button>
      <button v-else class="pomodoro-break-active" >Отдых</button>


      <radial-progress-bar class="pomodoro-bar" :diameter="220"
                           :total-steps="totalSeconds"
                           :completed-steps="completedSteps"
                           :inner-stroke-color="'#1b4ccb'"
                           :inner-stroke-width="7"
                           start-color="white"
                           stop-color="#46494b"
                           :is-clockwise="false">
        <span id="pomodoro-min" class="pomodoro-minutes">{{displayMinutes}}</span>
        <span class="pomodoro-colon">:</span>
        <span id="pomodoro-sec" class="pomodoro-seconds">{{displaySeconds}}</span>
      </radial-progress-bar>

      <div class="pomodoro-buttons">
        <button v-if="notRunning" class="pomodoro-start" @click="start" :disabled="buttonOn">Старт</button>
        <button v-else class="pomodoro-stop" @click="stop">Стоп</button>
        <button class="timer-options" @click="showModal">Опции</button>
      </div>

      <Options ref="modal" v-model:time="time" v-model:breaks="breaks" v-model:sessions="sessions"/>


      <ul class="personal-options">
        <li>
          <p>Время на работу: {{time}} мин.</p>
          <p>Время на перерыв: {{breaks}} мин.</p>
        </li>
        <li>
          <p>Количество циклов: {{sessions}}</p>
          <p>Цикл: {{session}}</p>
        </li>
      </ul>

    </div>

  </div>
</template>


<script>
import RadialProgressBar from 'vue-radial-progress';
import Options from "@/components/Options";
export default {
  name: "PersonalTimer",
  components: {Options, RadialProgressBar},
  created() {
    document.title = "Your Timer";
  },
  data(){
    return {
      // время в секундах (начальное)
      total: 0,
      // время, введенное пользователем
      time: 0,
      // работает таймер или нет
      notRunning: true,
      // интервал таймера
      timerGo: null,
      // сессия
      session: 0,
      // кол-во сессий
      sessions: 0,
      // активация кнопки Старт/Стоп
      buttonOn: true,
      // активация/деактивация кнопки mode
      buttonOnStart: true,
      // активация/деактивация кнопки перерыв
      buttonOnPause: true,
      // кнопки выключены до заполнения опций
      buttonBefore: true,
      // кол-во сессий перед окончанием
      breaks: 0,
      // начальный режим
      mode: '',
      // кол-во секунд всего для прогресса
      totalSeconds: null
    }},
  computed: {
    // пересчет минут
    displayMinutes(){
      const minutes = Math.floor(this.total / 60);
      return this.formatTime(minutes);
    },
    // пересчет секунд
    displaySeconds(){
      const seconds = this.total % 60;
      return this.formatTime(seconds);
    },
    // оставшиеся шаги для прогресса
    completedSteps(){
      return this.totalSeconds - this.total;
    },
  },
  methods: {
    // показать модальное окно
    // + активация кнопок только при заполнении опций
    showModal(){
      this.$refs.modal.show = true;
      this.buttonOnPause = true;
      this.buttonOnStart = true;
      return this.buttonBefore = false;
    },
    // форматирование таймера
    formatTime(time){
      if  (time < 10) {
        return '0' + time;
      }
      return time.toString();
    },
    // старт таймера
    start(){
      // звук кнопки
      new Audio('/timer/sounds/button-sound.mp3').play();

      this.notRunning = false;
      this.timerGo = setInterval(() => {
        // остановка при 0 секунд
        if (this.total <= 0) {

          // автоматическая смена режима
          // после указанных рабочих циклов полный стоп
          switch (this.mode) {
            case 'work':
              if (this.session % this.sessions === 0) {
                // сброс до начальных значений после завершения
                this.buttonOnPause = false;
                this.session = 0;
                this.sessions = 0;
                this.time = 0;
                this.breaks = 0;
                this.stop();
                new Audio('/timer/sounds/relax.mp3').play();
              } else {
                this.timerMode('break');
              }
              break;
            default:
              this.timerMode('work');
          }
          return;
        }
        // уменьшение секунд на 1
        this.total -= 1;
      }, 1000);

    },
    // остановка таймера
    stop(){
      // звук кнопки
      new Audio('/timer/sounds/button-sound.mp3').play();
      this.notRunning = true;
      clearInterval(this.timerGo);
    },
    // выбор метода
    timerMode(mode){
      this.mode = mode;

      switch (mode) {
        case 'work':
          // this.buttonOnPause = true;
          this.buttonOnStart = false;
          this.buttonOn = false;
          this.total = this.time * 60;
          this.totalSeconds = this.time * 60;
          // добавление к сессии
          this.session ++;
          new Audio('/timer/sounds/work.mp3').play();
          break;
        case 'break':
          this.buttonOnPause = false;
          this.buttonOn = false;
          this.buttonOnStart = true;
          this.total = this.breaks  * 60;
          this.totalSeconds = this.breaks * 60;
          new Audio('/timer/sounds/break.mp3').play();
          break;
        default:
          this.total = 0;
          this.totalSeconds = 0;
      }
    },
    // изменение title страницы
    changeTitle (minutes, seconds) {
      document.title = minutes + ':' + seconds;
    }
  },
  watch: {
    // отслеживание пуска таймера для изменения title
    total(){
      if (this.notRunning === false) {
        this.changeTitle(this.displayMinutes, this.displaySeconds)
      }
    },
  }
}
</script>


