<template>
  <div class="music">

    <h1 class="music-header">Музыка</h1>
    <p class="music-text"> Здесь можно включить фоновую музыку.</p>

    <div class="music-box">
      <MusicItem v-for="song in songs" :key="song.id" :author="song.author" :title="song.title"
                 :time="song.id === current.id && current.times || formatTimes(song.time)"
                 :is-active="song.id === current.id"
                 :track-state="song.id === current.id && isPlaying === true ? 'playing' : 'paused'"
                 @toggle="toggle" @selectTrack="selectTrack(song)"/>
    </div>
  </div>
</template>

<script>
import MusicItem from "@/components/MusicItem";
import songs from "@/assets/music";

export default {
  name: "Music",
  components: {MusicItem},
  data(){
    return {
      // загрузка песен из music.js
      songs: songs,
      // объект c текущей песней
      current: {},
      // проверка, включена ли музыка
      isPlaying: false,
      // создание класса Аудио
      player: new Audio(),
    }
  },
  methods: {
    // добавление 0 в начале времени
    format(time){
      if (time < 10) {
        return '0' + time;
      }
      return time.toString();
    },
    // форматирование времени
    formatTimes(time) {
      let hours = Math.floor(time / 60 / 60);
      let minutes = Math.floor(time / 60 % 60);
      let seconds = (time % 60).toString();

      return time = `${hours}:${this.format(minutes)}:${this.format(seconds)}`;
    },
    // подсчет текущего времени и остановка при конце песни
    listenersWhenPlay(song) {
      this.player.addEventListener("timeupdate", () => {
        // playerTimer - это текущее время песни на повышение
        let playerTimer = Math.floor(this.player.currentTime);

        // currentlyTimes - это разница (то есть уменьшение времени песни)
        let currentlyTimes = Math.floor(song.time - playerTimer);

        // отформатированное время песни - 1:10:03
        this.current.times = this.formatTimes(currentlyTimes);
        return this.current.times;
      });
      this.player.addEventListener(
          "ended",
          () => {
            this.isPlaying = !this.isPlaying;
          }
      );
    },
    // если выбранный трек уже активен - переключаем его
    toggle() {
      if (this.current) {
        // если он уже играет, то ставим на паузу
        if (this.isPlaying ) {
          this.player.pause();
        } // или если на паузе, начинаем играть
        else {
          this.player.play();
        }
        this.isPlaying = !this.isPlaying;
      }
    },
    // выбор текущего трека
    selectTrack(song) {
      // сброс текущего трека
      if (this.current) {
        this.player.src = this.current.src;
        this.player.pause();
        this.player.currentTime = 0;
      }

      // поменять текущий трек
      this.current = song;

      // начать менять время
      // начать играть трек
        this.player.src = this.current.src;
        this.isPlaying = true;
        this.player.play();

      this.listenersWhenPlay(song);
    },
  },
}
</script>


