<template>

  <transition name="fade">
    <div v-if="show" class="preloader">
      <div class="looping-rhombuses-spinner">
        <div class="rhombus"></div>
        <div class="rhombus"></div>
        <div class="rhombus"></div>
      </div>

      <p>Загрузка таймеров..</p>
    </div>
  </transition>

</template>

<script>
export default {
  name: "Loading",
  data(){
    return {
      show: true
    }
  },
  mounted(){
    if(Boolean(this.show)) this.showToggle();
  },
  methods: {
    showToggle(){
      setTimeout(() => {
        this.show = false
      }, 1200)
    }
  }
}
</script>


